'use strict';

import {Router} from 'express';
import * as controller from './user.controller';
import * as auth from '../../auth/auth.service';

var router = new Router();

router.get('/', auth.isAuthenticated(), controller.index);
router.get('/me', auth.isAuthenticated(), controller.me);
router.get('/:username', controller.show);

// Old endpoint to save - Temporary
router.put('/', auth.isAuthenticated(), controller.update);

// New endpoints
router.get('/skill', auth.isAuthenticated(), controller.searchBySkill);
router.put('/:username/skill', auth.isAuthenticated(), controller.updateSkill);
router.post('/:username/skill', auth.isAuthenticated(), controller.createSkill);
router.delete('/:username/skill', auth.isAuthenticated(), controller.deleteSkill);

router.put('/:username/language', auth.isAuthenticated(), controller.updateLanguage);
router.post('/:username/language', auth.isAuthenticated(), controller.createLanguage);
router.delete('/:username/language', auth.isAuthenticated(), controller.deleteLanguage);

router.put('/:username/education', auth.isAuthenticated(), controller.updateEducation);
router.post('/:username/education', auth.isAuthenticated(), controller.createEducation);
router.delete('/:username/education', auth.isAuthenticated(), controller.deleteEducation);

router.put('/:username/certification', auth.isAuthenticated(), controller.updateCertification);
router.post('/:username/certification', auth.isAuthenticated(), controller.createCertification);
router.delete('/:username/certification', auth.isAuthenticated(), controller.deleteCertification);

router.put('/:username/experience', auth.isAuthenticated(), controller.updateExperience);
router.post('/:username/experience', auth.isAuthenticated(), controller.createExperience);
router.delete('/:username/experience', auth.isAuthenticated(), controller.deleteExperience);

router.post('/:username/hobby', auth.isAuthenticated(), controller.createHobby);
router.delete('/:username/hobby', auth.isAuthenticated(), controller.deleteHobby);

//Simple fields
router.put('/:username/aboutme', auth.isAuthenticated(), controller.updateAboutMe);
router.put('/:username/summary', auth.isAuthenticated(), controller.updateSummary);
router.put('/:username/facebook', auth.isAuthenticated(), controller.updateFacebook);
router.put('/:username/linkedin', auth.isAuthenticated(), controller.updateTwitter);
router.put('/:username/twitter', auth.isAuthenticated(), controller.updateLinkedin);
router.put('/:username/skype', auth.isAuthenticated(), controller.updateSkype);





module.exports = router;
